export const query = graphql`
query{
  allWordpressPage(filter: {wordpress_id: {eq: 4}}) {
    edges {
      node {
        acf {
          welcome_intro
          expertise_block_1 {
            call_out_title
            content
            title
          }
          expertise_block_2 {
            call_out_title
            content
            title
          }
          expertise_block_3 {
            call_out_title
            content
            title
          }
          testimonials{
            comment
            from
          }
        }
      }
    }
  }
  allWordpressWpWork(limit: 10){
    edges{
      node{
        title
        slug
      }
    }
  }
}
`

export default App;
